.wp-block-eedee-block-gutenslider.content-change {
	height: 100%;

	.slide-content {
		// height: 100%;
		width: auto;
		display: flex;
		flex-direction: column;

		padding-top: var(--gutenslider-padding-y);
		padding-bottom: var(--gutenslider-padding-y);
		padding-left: var(--gutenslider-padding-x);
		padding-right: var(--gutenslider-padding-x);
	}

	.swiper-wrapper {
		> *:not(.swiper-slide) {
			display: none;
		}
	}
}

.wp-block-eedee-block-gutenslider.content-change.adaptive-height {
	.swiper-wrapper {
		align-content: center;
	}

	.eedee-background-div {
		min-height: unset;
	}

	.swiper-slide {
		height: unset;
	}

	.eedee-background-div video,
	.eedee-background-div img {
		height: 100%;
		user-select: none;
	}
}
