.wp-block-eedee-block-gutenslider.slides-auto {
	position: relative;

	.eedee-swiper-outer {
		height: var(--gutenslider-min-height);
	}

	.wp-block-eedee-block-gutenslide {
		width: auto;
		height: var(--gutenslider-min-height);
		display: block;
	}

	.swiper-wrapper {
		height: var(--gutenslider-min-height);
		& > .swiper-slide {
			height: var(--gutenslider-min-height);

			&.ed-bg-video {
				width: auto !important;

				.eedee-background-div {
					width: calc(var(--ed-vw) / var(--ed-vh) * var(--gutenslider-min-height));
				}
			}
		}
	}

	.swiper {
	  width: 100%;
	  height: var(--gutenslider-min-height);
	}

	.swiper-zoom-container {
		width: auto;
		height: var(--gutenslider-min-height);
	}

	.swiper-slide .swiper-zoom-container > div {
		height: var(--gutenslider-min-height);
		width: auto;
	}

	//overwrite style
	.wp-block-eedee-block-gutenslide {
		min-width: var(--slide-min-width);

		.slide-content {
			overflow: hidden;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0;
			height: auto;
		}

		&.embed-background {
			iframe {
				height: var(--gutenslider-min-height);
				max-height: unset !important;
			}
		}
	}

	.eedee-background-div {
		position: relative;
		height: var(--gutenslider-min-height);
		width: auto;
		justify-content: center;
		align-content: center;
		display: flex;

		.simpleParallax {
			height: var(--gutenslider-min-height);
		}

		&.no-media-background {
      min-width: 100%;
			.slide-content {
				height: var(--gutenslider-min-height);
			}
		}

		img {
			height: var(--gutenslider-min-height);
			//overwrite
			min-height: var(--gutenslider-min-height);
		}

		video {
			height: var(--gutenslider-min-height);
			//overwrite
			min-height: var(--gutenslider-min-height);
		}
	}
}
